<template>
    <div class="wrapper">
        <div class="title">
          我的地址
          <span class="title__create">
           <router-link to="/addressedit">新建</router-link> 
          </span>
      </div>
      <div class="address"
      v-if="addresslist.length === 0">
      暂无地址信息
    </div>
    <div
      class="address"
      v-if="addresslist.length > 0"
    >
        <div class="address__item"
        v-for="address in addresslist"
        :key="address._id"
        @click="()=>handaddressclick(address._id)">
          <p class="address__item__basic">{{address.name}}
            <span class="address__item__phone">{{address.phone}}</span>
          </p>
          <p class="address__item__address">
            {{address.city}}{{address.department}}{{address.houseNumber}}
          </p>
          <div class="iconfont">
          &#xe6f2;
        </div>
        </div>
        </div>
    </div>
    <docker :currentindex="3"/>
</template>
<script>
import docker from '../../components/docker.vue'
import {get} from '../../utils/request'
import { ref } from 'vue'
import { useRouter } from 'vue-router'


//地址列表
const useaddresslisteffect =()=>{
  const addresslist = ref([])
   const getaddresslist = async()=>{
    const result = await get('/api/user/address')
    if(result?.errno ===0 && result?.data?.length){
    addresslist.value = result.data;
   
  }
   }
   return{getaddresslist,addresslist}
}
export default {
        name:'address',
        components:{docker},
        setup(){
          const router = useRouter();
          const{addresslist,getaddresslist} = useaddresslisteffect()
          getaddresslist()
          const handaddressclick = (id)=>{
             router.push(`/addressedit?id=${id}`)
          }
          return{addresslist,handaddressclick}
        }
    }
        
</script>
<style lang="scss" scoped>
@import  '../../style/mixins.scss';
.wrapper{
    overflow:auto;
   @include  fix-content;
    background: rgba(248,248,248);
  }
  .title{
    position: relative;
      font-size: 0.16rem;
      background: #fff;
      color: #333;
      line-height: 0.44rem;
      text-align: center;
      &__create{
        position: absolute;
        right: .18rem;
        font-size: .14rem;
        a{
          text-decoration: none;
          color: #333;
        }
      }
  }
  .address{
    margin-top: .16rem .18rem 0 .18rem;
    &__item{
      position: relative;
      box-sizing: border-box;
      padding:.18rem .63rem .18rem .16rem ;
      margin-bottom: .16rem;
      background: #fff;
      border-radius: .04rem;
      &__basic{
        margin: 0;
        line-height: .2rem;
        font-size: .14rem;
        color: #999;
      }
      &__phone{
        margin-left: .66rem;
      }
      &__address{
        line-height: .2rem;
        margin:.08rem 0 0 0;
        font-size: .14rem;
        color:#999;
      }
     
    }
    .iconfont{
      transform: rotate(180deg);
    position: absolute;
    right: .16rem;
    top: .44rem;
    color: #333;
    font-size: .2rem;
      }
    
  }
  .empty{
            text-align: center;
            line-height: .5rem;
            margin-top: .5rem;
            font-size: .18rem;
            color: #999;
        }
  
</style>
